<template>
    <div class="form__counter">
        <button type="button" aria-label="Убрать один товар" @click="decrement" style="cursor: pointer">
            <svg width="10" height="10" fill="currentColor">
            <use xlink:href="#icon-minus"></use>
            </svg>
        </button>

        <label><input type="text" :value="amount" name="count"></label>

        <button type="button" aria-label="Добавить один товар" @click="increment" style="cursor: pointer">
            <svg width="10" height="10" fill="currentColor">
            <use xlink:href="#icon-plus"></use>
            </svg>
        </button>
    </div>
</template>

<script>
export default {
  props: ['amount', 'size'],

  methods: {
    increment() {
      this.$emit('update:amount', this.amount + 1);
    },
    decrement() {
      if (this.amount <= 0) return;
      this.$emit('update:amount', this.amount - 1);
    },
  },
};
</script>
