<template>
  <div>
    <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">

    <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId"
                   :color.sync="filterColor" :page.sync="page"/>

    <section class="catalog">
      <div v-show="productsLoading">
        <img src="@/img/Fidget-spinner.gif" alt="...идет загрузка" style="width: 100px">
        <br>
        ...идет загрузка
      </div>
      <div v-show="productsLoadingFailed">
        Ошибка при загрузке
        <button @click="loadproducts">Попробовать еще раз</button>
      </div>
      <ProductList :products="products" />
      <BasePagination :page.sync="page" :count="countProducts" :per-page="productsPerPage"/>
    </section>
    </div>
    </main>
  </div>
</template>

<script>
import ProductList from '@/components/ProductList.vue';
import BasePagination from '@/components/BasePagination.vue';
import ProductFilter from '@/components/ProductFilter.vue';
import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { ProductList, BasePagination, ProductFilter },

  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColor: '#000',
      page: 1,
      productsPerPage: 3,
      productsData: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },

  computed: {
    products() {
      return this.productsData
        ? this.productsData.items.map((product) => ({
          ...product,
          image: product.image.file.url,
        }))
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },

  methods: {
    loadproducts() {
      clearTimeout(this.loadProductsTimer);
      this.productsLoading = true;
      this.productsLoadingFailed = false;
      this.loadProductsTimer = setTimeout(() => {
        axios.get(`${API_BASE_URL}/api/products`, {
          params: {
            page: this.page,
            limit: this.productsPerPage,
            categoryId: this.filterCategoryId,
            minPrice: this.filterPriceFrom,
            maxPrice: this.filterPriceTo,
            colorId: this.filterColor,
          },
        })
          .then((res) => { this.productsData = res.data; })
          .catch(() => { this.productsLoadingFailed = true; })
          .then(() => { this.productsLoading = false; });
      }, 500);
    },
  },

  watch: {
    page() {
      this.loadproducts();
    },
    filterCategoryId() {
      this.loadproducts();
    },
    filterPriceFrom() {
      this.loadproducts();
    },
    filterPriceTo() {
      this.loadproducts();
    },
    filterColor() {
      this.loadproducts();
    },
  },

  created() {
    this.loadproducts();
  },
};
</script>
