import Vue from 'vue';
import Vuex from 'vuex';
// import products from '@/data/products';
import axios from 'axios';
import { API_BASE_URL } from '@/config';
import { setTimeout } from 'core-js';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    cartProducts: [],
    userAccessKey: null,
    cartProductsData: [],
    cartLoading: false,
    orderInfo: {
      basket: {
        items: [],
      },
    },
  },
  mutations: {
    updateOrderInfo(state, orderInfo) {
      state.orderInfo = orderInfo;
    },
    resetCart(state) {
      state.cartProducts = [];
      state.cartProductsData = [];
    },
    updateCartProductAmount(state, { productId, amount }) {
      const item = state.cartProducts.find((res) => res.productId === productId);
      if (item) {
        item.amount = amount;
      }
    },
    deleteCartProduct(state, productId) {
      state.cartProducts = state.cartProducts.filter((res) => res.productId !== productId);
    },
    updateUserAccessKey(state, accessKey) {
      state.userAccessKey = accessKey;
    },
    updateCartProductsData(state, productsData) {
      state.cartProductsData = productsData;
    },
    syncCartProducts(state) {
      state.cartProducts = state.cartProductsData.map((item) => ({
        productId: item.product.id,
        amount: item.quantity,
      }));
    },
    updateCartLoading(state, val) {
      state.cartLoading = val;
    },
  },
  getters: {
    cartDetailProducts(state) {
      // console.log(state.cartProductsData[0].product);
      return state.cartProducts.map((item) => {
        // console.log(item.productId);
        const productXXX = state.cartProductsData.find((p) => p.product.id === item.productId).product;

        return {
          ...item,
          product: {
            ...productXXX,
            image: productXXX.image.file.url,
          },
        };
      });
    },
    cartTotalPrice(state, getters) {
      return getters.cartDetailProducts.reduce((acc, res) => (res.amount * res.product.price) + acc, 0);
    },
    cartTotalProducts(state) {
      return state.cartProducts.length;
    },
    cartLoadingState(state) {
      return state.cartLoading;
    },
    orderInfoData(state) {
      return state.orderInfo;
    },
  },
  actions: {
    loadOrderInfo(context, orderId) {
      axios.get(`${API_BASE_URL}/api/orders/${orderId}`, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((response) => {
          context.commit('updateOrderInfo', response.data);
        });
    },
    loadCart(context) {
      context.commit('updateCartLoading', true);
      return new Promise((resolve) => { setTimeout(resolve, 3000); })
        .then(() => axios.get(`${API_BASE_URL}/api/baskets`, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
          .then((res) => {
            if (!context.state.userAccessKey) {
              localStorage.setItem('userAccessKey', res.data.user.accessKey);
              context.commit('updateUserAccessKey', res.data.user.accessKey);
            }
            context.commit('updateCartProductsData', res.data.items);
            context.commit('syncCartProducts');
          })
          .then(() => { context.commit('updateCartLoading', false); }));
    },
    addProductToCart(context, { productId, amount }) {
      return new Promise((resolve) => { setTimeout(resolve, 1000); })
        .then(() => axios.post(`${API_BASE_URL}/api/baskets/products`, {
          productId,
          quantity: amount,
        }, {
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        })
          .then((res) => {
            context.commit('updateCartProductsData', res.data.items);
            context.commit('syncCartProducts');
          }));
    },
    updateCartProductAmount(context, { productId, amount }) {
      if (amount < 1) {
        return;
      }

      context.commit('updateCartProductAmount', { productId, amount });
      axios.put(`${API_BASE_URL}/api/baskets/products`, {
        productId,
        quantity: amount,
      }, {
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then((res) => {
          context.commit('updateCartProductsData', res.data.items);
        })
        .catch(() => {
          context.commit('syncCartProducts');
        });
    },
    deleteProductFromCart(context, productId) {
      return axios.delete(`${API_BASE_URL}/api/baskets/products`, {
        data: {
          productId,
        },
        params: {
          userAccessKey: context.state.userAccessKey,
        },
      })
        .then();
    },
  },
});
