<template>
    <li class="cart__item product">
        <div class="product__pic">
        <img :src="item.product.image" alt="item.product.title">
        </div>
        <h3 class="product__title">
        {{ item.product.title }}
        </h3>
        <p class="product__info product__info--color">
        Цвет:
        <span>
            <i style="background-color: #73B6EA"></i>
            Нежно-голубой
        </span>
        </p>
        <span class="product__code">
        Артикул: {{ item.product.id }}
        </span>

        <ProductAmount :amount.sync="amount" :class="{ product__counter: true }" />

        <b class="product__price">
            {{ item.product.price * item.amount | numberFormat }} ₽
        </b>

        <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="deleteProduct(item.productId)" style="cursor: pointer">
        <svg width="20" height="20" fill="currentColor">
            <use xlink:href="#icon-close"></use>
        </svg>
        </button>
    </li>
</template>

<script>
import numberFormat from '@/helpers/numberFormat';
import ProductAmount from '@/components/ProductAmount.vue';
// eslint-disable-next-line
import { mapMutations, mapActions } from 'vuex';

export default {
  props: ['item'],
  components: { ProductAmount },
  filters: { numberFormat },
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    /*
    ...mapMutations({ deleteProduct: 'deleteCartProduct' }),
        ...mapMutations(['deleteCartProduct']),
    ...mapActions(['deleteProductFromCart']),
    */
    deleteProduct(id) {
      // this.deleteCartProduct(id);
      this.$store.commit('deleteCartProduct', id);
      this.$store.dispatch('deleteProductFromCart', id);
    },
  },
};
</script>
